@import url(https://fonts.googleapis.com/icon?family=Material+Icons);
.material-icons.md-18 { font-size: 18px; }
.material-icons.md-24 { font-size: 24px; }
.material-icons.md-36 { font-size: 36px; }
.material-icons.md-48 { font-size: 48px; }

html, #root {
  width: 100%;
  height: 100%;
}

body {
  display: flex;
  flex-direction: column;
  align-items: center;
  background: linear-gradient(to top, rgb(54, 209, 220), rgb(91, 134, 229));
}

* {
  font-family: Roboto, sans-serif;
  color: white;
}

@font-face {
  font-family: Roboto;
  src: url(/Alisson-Augusto/Interface-Inspirada-no-Psp/static/media/Roboto-Light.0cea3982.ttf)
}

nav {
    display: flex;
    align-items: center;
    width: 100%;
    justify-content: flex-end;
    margin-bottom: 30px;
}

nav span, i {
    margin-left: 10px;
}
.menu-geral {
    display: flex;
    justify-content: space-around;
    width: 100%;
}
.menu-geral-item {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    cursor: pointer;
}

.menu-geral-category {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 10px;
}

.menu-geral-category i {
    margin: -5px;
    padding: 0px;
}

.menu-geral-category span {
    margin: 0px;
    padding: 0px;
}
.menu-item-header {
    display: flex;
    flex-direction: column;
    margin-left: 10px;
}

.title {
    font-size: 1.2rem;
    margin-bottom: 0px;
    margin-top: 0px;
}

.hr {
    border-style: solid;
    border-width: 0px 0px 1px 0px;
}

.menu-item-header p {
    margin: 0px;
}

.menu-item {
    display: flex;
    align-items: center;
    margin-bottom: 10px;
}

.menu-item i {
  margin: 0px;
}
span, p {
    text-shadow: 2px 2px 4px black;
}

nav span, img {
    margin-left: 10px;
}
